/* Transaction.css */

/* Container styles */
body {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    margin: 0;
    background-color: white; /* Optional background color for the whole page */
  }
  
  .container {
    max-width: 400px;
    padding: 20px;
    background-color: black;
    border: 1px solid #ddd;
    border-radius: 5px;
    text-align: center;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  /* Header styles */
  h2 {
   
    text-align: center;
    font-size: 24px;

    margin-bottom: 20px;
    color: #333;
  }
  
  /* Input styles */
  input[type="text"],
  input[type="number"] {
    justify-content: center;
    align-items: center;
    width: 90%;
    padding: 10px;
    margin-bottom: 10px;
    margin-left: 10px;
    border: 1px solid #ccc;
    border-radius: 3px;
    font-size: 16px;
  }
  
  /* Button styles */
  button {
    
    background-color: black;
    width: 100px; /* Set the width as needed */
    height: 30px;
    color: #fff;
    border: none;
    border-radius: 3px;
    padding: 12px 20px;
    font-size: 15px;
    cursor: pointer;
    margin-right: 2px;
    display: block; /* Add this to center horizontally */
    margin: 0 auto; /* Add this to center horizontally */
  }
  
  button:hover {
    background-color: #0056b3;
  }

  #main {
    background-color: white;
    border: 1px solid #ccc; /* Optional: Add a border for better 3D effect */
    border-radius: 10px; /* Adjust the radius to control the depth */
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5); 
    justify-content: center;
    align-items: center;
}
  
 
